
import { defineComponent, ref } from "vue";
import Headbar from "@/components/headbar/index.vue";
import { Dialog } from "vant";
import { useRouter } from "vue-router";

export default defineComponent({
  components: {
    Headbar,
  },
  setup() {
    const random = (length:number = 6) => [...Array(length)].map(() => Math.random().toString(36)[2]).join("");
    const router = useRouter();
    let sortValue = ref(0);
    let btnType = ref(1);
    let newCreateSceneName = ref(random(6));
    let showNewCreateScene = ref(false);
    let searchValue = ref("");
    let tipStatus = ref(false)
    let optionSort = [
      { text: "按更新时间", value: 0 },
      { text: "按创建时间", value: 1 },
      { text: "按文件名称", value: 2 },
    ];

    let sceneList = [
      { address: "城堡", date: "21-11-09" },
      { address: "斗兽场", date: "21-11-09" },
      { address: "骑马", date: "21-11-09" },
      { address: "硝烟", date: "21-11-09" },
      { address: "城堡1", date: "21-11-09" },
      { address: "斗兽场1", date: "21-11-09" },
      { address: "骑马1", date: "21-11-09" },
      { address: "硝烟1", date: "21-11-09" },
    ];

    let templateList = [
      { address: "城堡" },
      { address: "斗兽场" },
      { address: "骑马" },
      { address: "硝烟" },
    ];

    const clickScene = () => {
      btnType.value = 1;
      console.log("clickScene");
    };
    const clickCreate = () => {
      btnType.value = 2;
      console.log("clickCreate");
    };

    const goback = () => {
      history.go(-1);
      router.push("/home?f=1");
    };

    const clickSceneItem = () => {
      Dialog.confirm({
        className: "confirm-scene-model",
        message: "是否确认打开场景？",
      })
        .then(() => {
          router.push("/loading");
        })
        .catch(() => {
        });
    };

    const clickTemplateItem = () => {
      showNewCreateScene.value = true;
      newCreateSceneName.value = random(8);
    };

    const closeCreateModel = () => {
        showNewCreateScene.value = false;
    }

    const onBeforeClose = (action: string, done: Function) => {
        if (action === "confirm") {
            if (tipStatus.value) {
                showNewCreateScene.value = true;
            } else {
                showNewCreateScene.value = false;
            }
        } else {
            showNewCreateScene.value = false;
        }
    }

    const confirmCreateModel = () => {
        if (newCreateSceneName.value.length) {
            tipStatus.value = false;
            showNewCreateScene.value = false;
            router.push({
                path: "/loading"
            })
        } else {
            tipStatus.value = true;
        }
    }

    const cancelCreateModel = () => {
        console.log(22)
        tipStatus.value = false;
    }

    const changeSceneName = () => {
        tipStatus.value = false;
    }

    return {
      sortValue,
      optionSort,
      btnType,
      sceneList,
      templateList,
      newCreateSceneName,
      showNewCreateScene,
      searchValue,
      tipStatus,
      clickScene,
      clickCreate,
      goback,
      clickSceneItem,
      clickTemplateItem,
      closeCreateModel,
      confirmCreateModel,
      cancelCreateModel,
      onBeforeClose,
      changeSceneName
    };
  },
});
